        left: -2%; } }
  .bg-fixed .deco02 {
    opacity: 0;
    width: 21%;
    top: 5.5%;
    right: -5%; }
    @media screen and (min-width: 769px) {
      .bg-fixed .deco02 {
        width: 15%;
