.js-bg-trigger.is-active .deco03, .js-bg-trigger.is-active .deco04 {
  animation-name: fadeIn;
  animation-duration: 1.5s;
  animation-delay: 0s;
  animation-fill-mode: forwards; }

.js-bg-trigger:not(.is-active) .deco03, .js-bg-trigger:not(.is-active) .deco04 {
  animation-name: fadeOut;
  animation-duration: 1.5s;
  animation-delay: 0s;
  animation-fill-mode: forwards; }

.js-bg-trigger.is-active.is-out .deco01, .js-bg-trigger.is-active.is-out .deco02 {
  animation-name: fadeIn;
  animation-duration: 1.5s;
  animation-delay: 0s;
  animation-fill-mode: forwards; }

.js-bg-trigger.is-active.is-in .deco01, .js-bg-trigger.is-active.is-in .deco02 {
  animation-name: fadeOut;
  animation-duration: 1.5s;
  animation-delay: 0s;
  animation-fill-mode: forwards; }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

